<template>
	<div class="dialog-wrapper">
		<el-dialog
			:visible.sync="dialogVisible"
			:width="width+'px'"
            :show-close="false"
            :before-close="closeDialog"
		>
        <template slot="title">
            <div class="dialog-header">
                <span></span>
                <span>{{ title }}</span>
                <img src="../assets/images/close.png" @click="closeDialog">
            </div>
            
        </template>
		<template>
            <slot></slot>
        </template>
		</el-dialog>
	</div>
</template>

<script>
export default {
	data() {
		return {};
    },
    props: {
        dialogVisible: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ""
        },
        width: {
            type: Number,
            default: 760
        }
    },
	created() {},
	mounted() {},
	computed: {},
	methods: {
        closeDialog() {
            this.$emit("closeDialog")
        }
    },
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog__header {
    height: 60px;
    background-color: #2a354e;
    box-sizing: border-box;
    font-size: 20px;
    color: #fff;
    padding: 0;
    line-height: 60px;
}
.dialog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
/deep/.el-dialog__body {
    padding: 40px 80px;
}
</style>
