<template>
	<div class="card-wrapper">
		<div class="header">
			<div class="title">产品列表</div>
			<div>
				<button class="primary-btn" @click="common_dialog_visible = true">
					生成通用卡
				</button>
				<button class="primary-btn" @click="requestSingle">
					生成单一卡
				</button>
			</div>
		</div>
		<div class="split-line"></div>
		<div class="main-content">
			<div class="search-area">
				<div class="input-area">
					<div class="input-item">
						<span>申请时间：</span>
						<el-date-picker
							v-model="searchForm.startTime"
							type="date"
							placeholder="选择日期"
						>
						</el-date-picker>
						<span style="margin: 0 10px;">至</span>
						<el-date-picker
							v-model="searchForm.finishTime"
							type="date"
							placeholder="选择日期"
						>
						</el-date-picker>
					</div>
					<div class="input-item">
						<span>产品：</span>
						<el-input
							placeholder="请输入"
							v-model="searchForm.productSeriesName"
						></el-input>
					</div>
					<div class="input-item">
						<span>申请人：</span>
						<el-select v-model="searchForm.applicant" placeholder="请选择">
							<el-option
								v-for="item in options"
								:key="item"
								:label="item"
								:value="item"
							>
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="search-btn" @click="searchCard">
					查询
				</div>
			</div>
			<div class="table-content">
				<el-table
					ref="multipleTable"
					:data="tableData"
					tooltip-effect="dark"
					style="width: 100%"
					:stripe="true"
				>
					<el-table-column label="序号" width="120" align="center">
						<template slot-scope="scope">{{
							scope.$index + 1 + (searchForm.page - 1) * 10
						}}</template>
					</el-table-column>
					<el-table-column prop="productSeriesName" label="产品">
					</el-table-column>
					<el-table-column prop="cardNo" label="卡号"> </el-table-column>
					<el-table-column
						prop="originalTimes"
						label="原个数"
						width="100"
						align="center"
					>
					</el-table-column>
					<el-table-column
						prop="remainingTimes"
						label="目前个数"
						width="100"
						align="center"
					></el-table-column>
					<el-table-column
						prop="applicationTime"
						label="生成时间"
						align="center"
					></el-table-column>
					<el-table-column
						prop="applicant"
						label="申请人"
						width="100"
						align="center"
					></el-table-column>
					<el-table-column prop="remark" label="备注"> </el-table-column>
				</el-table>
			</div>
			<div class="page-area">
				<el-pagination
					:current-page.sync="searchForm.page"
					background
					layout="->,prev, pager, next"
					prev-text="上一页"
					next-text="下一页"
					:total="total"
					@current-change="getCardByPage"
				>
				</el-pagination>
			</div>
		</div>
		<my-dialog
			:dialogVisible="single_dialog_visible"
			title="单一卡生成"
			@closeDialog="single_dialog_visible = false"
			:width="700"
		>
			<div class="dialog-content">
				<div class="form-content">
					<div class="form-item">
						<span>请选择产品：</span>
						<div>
							<el-select
								v-model="singleCard.productSeriesId"
								placeholder="请选择"
								value-key="id"
							>
								<el-option
									v-for="item in product_list"
									:key="item.id"
									:label="item.chName"
									:value="item.id"
								></el-option>
							</el-select>
							<el-input
								placeholder="请输入激活数量"
								class="left-space"
								v-model="singleCard.originalTimes"
							></el-input>
						</div>
					</div>
					<div class="form-item">
						<span>申请人：</span>
						<div>
							<el-select
								placeholder="请选择"
								class="auto-width"
								v-model="singleCard.applicant"
							>
								<el-option
									v-for="item in options"
									:key="item"
									:label="item"
									:value="item"
								>
								</el-option>
							</el-select>
						</div>
					</div>
					<div class="form-item">
						<span>备注：</span>
						<div>
							<el-input
								type="textarea"
								:autosize="{ minRows: 3, maxRows: 3 }"
								v-model="singleCard.remark"
							></el-input>
						</div>
					</div>
				</div>
				<div class="btn-area">
					<button class="square-btn" @click="single_dialog_visible = false">
						取消
					</button>
					<button class="square-btn primary space-area" @click="addSingle">
						确定
					</button>
				</div>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="common_dialog_visible"
			title="通用卡生成"
			@closeDialog="common_dialog_visible = false"
		>
			<div class="dialog-content">
				<div class="form-content">
					<div class="form-item">
						<span>请输入激活数量：</span>
						<div>
							<el-input
								placeholder="请输入激活数量"
								class="all-width"
								v-model="commonCard.originalTimes"
							></el-input>
						</div>
					</div>
					<div class="form-item">
						<span>申请人：</span>
						<div>
							<el-select
								v-model="commonCard.applicant"
								placeholder="请选择"
								class="auto-width"
								value-key="value"
							>
								<el-option
									v-for="item in options"
									:key="item"
									:label="item"
									:value="item"
								>
								</el-option>
							</el-select>
						</div>
					</div>
					<div class="form-item">
						<span>备注：</span>
						<div>
							<el-input
								type="textarea"
								:autosize="{ minRows: 3, maxRows: 3 }"
								v-model="commonCard.remark"
								resize="none"
							></el-input>
						</div>
					</div>
				</div>
				<div class="btn-area">
					<button class="square-btn" @click="common_dialog_visible = false">
						取消
					</button>
					<button class="square-btn primary space-area" @click="addCard">
						确定
					</button>
				</div>
			</div>
		</my-dialog>
	</div>
</template>

<script>
import myDialog from "../../components/my_dialog.vue";
import { addCommonCard, getCardList, getApplicant } from "@/api/card";
import { getProductShow } from "@/api/product";
import { formatTime } from "@/utils/index";
export default {
	data() {
		return {
			common_dialog_visible: false,
			single_dialog_visible: false,
			start_time: "",
			current_page: 1,
			product_name: "",
			sn_code: "",
			options: [],
			sn_loading: false,
			tableData: [],
			total: 0,
			searchForm: {
				startTime: "",
				finishTime: "",
				productSeriesName: "",
				size: 10,
				page: 1,
				applicant: "",
			},
			commonCard: {
				originalTimes: "",
				applicant: "",
				remark: "",
				type: 1,
				productSeriesId: "",
			},
			singleCard: {
				applicant: "",
				originalTimes: "",
				productSeriesId: "",
				remark: "",
				type: 0,
			},
			product_list: [],
		};
	},
	components: {
		myDialog,
	},
	created() {},
	mounted() {
		this.$store.commit("setRouteName", {
			name: "卡号管理",
			path: "/cardManage",
		});
		this.getCard();
		this.init();
	},
	computed: {
		userName() {
			return this.$store.state.userName;
		},
	},
	methods: {
		requestSN(value) {
			this.sn_loading = true;
			setTimeout(() => {
				this.sn_loading = false;
			}, 3000);
			console.log(value);
		},
		addCard() {
			if (this.commonCard.originalTimes == "") {
				this.$message({
					type: "warning",
					message: "激活数量不能为空!",
				});
				return;
			} else if (!/^[1-9]\d*$/.test(this.commonCard.originalTimes)) {
				this.$message({
					type: "warning",
					message: "激活数量必须为正整数!",
				});
				return;
			} else if (this.commonCard.originalTimes > 10000) {
				this.$message({
					type: "warning",
					message: "激活数量不能大于10000!",
				});
				return;
			} else if (this.commonCard.applicant == "") {
				this.$message({
					type: "warning",
					message: "申请人不能为空!",
				});
				return;
			}
			this.commonCard.operator = this.userName;
			addCommonCard(this.commonCard).then((res) => {
				this.$message({
					type: "success",
					message: "新增通用卡成功",
				});
				this.commonCard = {
					originalTimes: "",
					applicant: "",
					remark: "",
					type: 1,
				};
				this.getCard();
				this.common_dialog_visible = false;
			});
		},
		requestSingle() {
			this.single_dialog_visible = true;
			getProductShow().then((res) => {
				this.product_list = res.data.data;
			});
		},
		addSingle() {
			if (this.singleCard.productSeriesId == "") {
				this.$message({
					type: "warning",
					message: "产品不能为空!",
				});
				return;
			} else if (this.singleCard.originalTimes == "") {
				this.$message({
					type: "warning",
					message: "激活数量不能为空!",
				});
				return;
			} else if (!/^[1-9]\d*$/.test(this.singleCard.originalTimes)) {
				this.$message({
					type: "warning",
					message: "激活数量必须为正整数!",
				});
				return;
			} else if (this.singleCard.originalTimes > 10000) {
				this.$message({
					type: "warning",
					message: "激活数量不能大于10000!",
				});
				return;
			} else if (this.singleCard.applicant == "") {
				this.$message({
					type: "warning",
					message: "申请人不能为空!",
				});
				return;
			}
			this.singleCard.operator = this.userName;
			addCommonCard(this.singleCard).then((res) => {
				this.$message({
					type: "success",
					message: "新增单一卡成功",
				});
				this.singleCard = {
					applicant: "",
					originalTimes: "",
					productSeriesId: "",
					remark: "",
					type: 0,
				};
				this.getCard();
				this.single_dialog_visible = false;
			});
		},
		getCard() {
			getCardList(this.searchForm).then((res) => {
				this.total = res.data.data.count;
				this.tableData = res.data.data.rowList;
			});
		},
		init() {
			getApplicant().then((res) => {
				this.options = res.data.data;
				console.log(res);
			});
		},
		searchCard() {
			let data = this.searchForm;
			data.page = 1;
			if (data.startTime) {
				data.startTime = formatTime(data.startTime, 10);
			}
			if (data.finishTime) {
				data.finishTime = formatTime(data.finishTime, 10);
			}
			getCardList(data).then((res) => {
				this.total = res.data.data.count;
				this.tableData = res.data.data.rowList;
			});
		},
		getCardByPage(page) {
			let data = this.searchForm;
			data.page = page;
			if (data.startTime) {
				data.startTime = formatTime(data.startTime, 10);
			}
			if (data.finishTime) {
				data.finishTime = formatTime(data.finishTime, 10);
			}
			getCardList(data).then((res) => {
				this.total = res.data.data.count;
				this.tableData = res.data.data.rowList;
			});
		},
	},
};
</script>

<style lang="scss" scoped>
.card-wrapper {
	.header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.title {
			font-size: 24px;
			color: #313552;
			font-weight: bold;
		}
		.primary-btn {
			width: 154px;
			height: 44px;
			line-height: 42px;
			font-size: 18px;
			color: #313552;
			border-radius: 22px;
			background-color: rgba(66, 90, 203, 0.1);
			border: 1px solid rgba(49, 53, 82, 0.4);
			font-size: 18px;
			font-family: "alifont";
			margin-left: 32px;
		}
		.primary-btn:active {
			background-color: #425acb;
			color: #fff;
			border-color: #425acb;
		}
	}
	.split-line {
		height: 1px;
		background-color: #d9d9d9;
		margin: 36px 0 32px 0;
	}
	.main-content {
		.search-area {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 28px;
			.input-area {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				.el-input,
				.el-select {
					width: 220px;
				}
				.input-item {
					margin-right: 25px;
					span {
						margin-right: 15px;
						font-size: 20px;
						color: #8c8c8c;
					}
				}
				.input-item:last-child {
					margin-right: 0px;
				}
			}
			.search-btn {
				width: 120px;
				height: 44px;
				line-height: 44px;
				font-size: 18px;
				color: #fff;
				background-color: #425acb;
				border-radius: 5px;
				text-align: center;
				cursor: pointer;
			}
		}
		.table-content {
		}
		.page-area {
			margin-top: 33px;
		}
	}
	.dialog-content {
		.form-content {
			.form-item {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-bottom: 24px;

				& > div {
					flex: 1;
					display: flex;
					justify-content: space-between;
				}
				span {
					width: 115px;
					text-align: right;
					display: inline-block;
					margin-right: 10px;
				}
				.el-select {
					width: 200px;
				}
				.el-input {
					width: 175px;
				}
				.all-width,
				.auto-width {
					width: 100%;
				}
				.left-space {
					margin-left: 24px;
				}
			}
		}
		.btn-area {
			display: flex;
			justify-content: center;
			margin-top: 40px;
			.space-area {
				margin-left: 80px;
			}
		}
		.square-btn {
			width: 120px;
			height: 44px;
			line-height: 42px;
			font-size: 18px;
			color: #313552;
			border-radius: 4px;
			background-color: rgba(66, 90, 203, 0.1);
			border: 1px solid rgba(49, 53, 82, 0.4);
			font-size: 18px;
			font-family: "alifont";
			text-align: center;
			display: inline-block;
		}
		.primary {
			background-color: #425acb;
			color: #fff;
		}
	}
}
</style>
