import fetch from "../utils/fetch"

export function addCommonCard(data) {
    return fetch({
        url: "/card/generate",
        method: "post",
        data
    })
}

export function getCardList(data) {
    return fetch({
        url: "/card/cardList",
        method: "post",
        data
    })
}

export function getApplicant() {
    return fetch({
        url: "/card/getApplicant",
        method: "get",
    })
}